h1 {
    font-size: 30px;
    line-height: 40px;
}

h1, h2, h3, h4, h5, h6 {
    margin: auto;
    padding: 0;
    font-weight: 400;
}
.All{
    margin: auto;
    padding: 40px 10px;
    width: 100%;
    max-width: 1500px;
    gap:300px;
    overflow-x:hidden;
    
}

.videoText{
    margin: 20px auto 25px auto;
    line-height: 24px;
    font-style: italic;
    font-size: 16px;
}
.VideoContainer{
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    gap:1px;
    
}

.VideoRelatedMain{
    padding-top: 20px;
    max-height: 455px;
    overflow-y: scroll;
}

.VideoRelatedMain ul{
display:flex;
flex-direction:column;
gap:20px;
list-style-type: none;
padding-left:5px;
}
.VideoRelatedMain ul li a{
    display:flex;
    padding: 10px;
    border-radius: 3px;
    text-align: left;
    display: block;
    color: #333;
    gap:5px;
    
}




.VideoRelatedMain ul li a h4{
    padding-left: 20px;
    width: 70%;
    line-height: 24px;
    letter-spacing: 1px;
    font-weight: 700;
    font-size: 14px;
    display: inline-block;
    vertical-align: middle;
}

.VideoRelatedMain ul li a img{
    width: 30%;
    height: auto;
    border-radius: 0.25rem!important;
    display: inline-block;
    vertical-align: middle;
}
@media (max-width: 1000px){
    .videoItems{
        left:auto !important;
    }
}
@media (max-width: 768px), (max-device-width: 768px){
.VideoRelatedMain ul li a img {
    max-height: initial;
}
.VideoItemsTwo{
    top:20px;
}
.videoButtons{
    width:auto !important;
    gap:100px !important;
    
    top:-30px!important;
}
.previewInform2{
    width:auto !important;
    flex-wrap:wrap;
}
.videoText{
    padding-right:0 !important;
}
.videoTitle{
    padding-right:0 !important;
}
.VideoTags{
    margin-right:0 !important;
}

}








