}
.VideoTags{
    margin: 20px auto 0 auto;
    background: #F8F8F8;
    border: 1px solid #DDD;
    border-radius: 3px;
    text-align: center;
    overflow: hidden;
    

}
.VideoTagsList ul{
    display:flex;
    flex-wrap:wrap;
    gap:10px;
    margin:0;
    padding:0;
list-style-type: none;
justify-content: center;

}


.VideoTagsList ul li a{
    padding: 4px 12px 6px 12px;
    line-height: 20px;
    border-radius: 30px;
    background: rgba(0, 0, 0, 0.07);
    color: #333;
    font-size: 12px;
    
    transition: all 0.2s ease-in-out;
}
.VideoTagsList ul li a:hover{
    text-decoration: none;
    background: #4054b4;
    color: #FFF
}

.VideoTags h3{
    padding: 7px 10px;
    line-height: 20px;
    background: #EEE;
    border-bottom: 1px solid #DDD;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    font-size: 14px;
    display: block;
}

.VideoTagsList{
    padding: 5px 10px 20px 10px;
}
