.listCategory{
 border:1px solid black;
  position:absolute;
  background-color: white;
}
.listCategory ul{
list-style-type: none;
margin:0;


display:flex;
flex-direction: column;
gap:5px;
padding:6px;
justify-content: center;

}
.listCategory ul li{
  padding:5px;
  cursor:pointer;
}
.listCategory ul li:hover{
  background-color:black;
  color:white;
  
}
#brandSafeTrue{
  color:green;
  cursor:pointer;
  }
  #brandSafeFalse{
    color:red;
    cursor:pointer;
    }
#brandSafeTrue:hover{
text-decoration:underline;
}
#brandSafeFalse:hover{
  text-decoration:underline;
  }
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
